h1,
h2,
h3,
h4,
h5,
h6 {
  border: 0;
  font: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
  -webkit-text-size-adjust: none;
  vertical-align: baseline;
}

h1, .heading00 {
  font-weight: normal;
  font-family: "Gotham", Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 400;
  font-size: 3rem;
  letter-spacing: -0.4px;
  line-height: 3.5rem;
}

@media print, screen and (min-width: 48em) {
  h1, .heading00 {
    font-size: 3.75rem;
    letter-spacing: -0.8px;
    line-height: 4.125rem;
  }
}


h2, .heading01 {
  font-weight: normal;
  font-family: "Gotham", Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 400;
  font-size: 2.25rem;
  letter-spacing: -0.4px;
  line-height: 2.75rem;
}

@media print, screen and (min-width: 48em) {
  
  h2, .heading01 {
    font-size: 3rem;
    letter-spacing: -0.8px;
    line-height: 3.5rem;
  }
}


h3, .heading02 {
  font-weight: normal;
  font-family: "Gotham", Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 400;
  font-size: 1.75rem;
  letter-spacing: -0.4px;
  line-height: 2.25rem;
}

@media print, screen and (min-width: 48em) {
  
  h3, .heading02 {
    font-size: 1.875rem;
    letter-spacing: -0.4px;
    line-height: 2.375rem;
  }
}


h4, .heading03 {
  font-weight: normal;
  font-family: "Gotham", Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 400;
  font-size: 1.25rem;
  letter-spacing: -0.4px;
  line-height: 1.5rem;
}

@media print, screen and (min-width: 48em) {
  
  h4, .heading03 {
    font-size: 1.5rem;
    letter-spacing: -0.4px;
    line-height: 1.875rem;
  }
}


h5, .heading04 {
  font-weight: normal;
  font-family: "Gotham", Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 400;
  font-size: 1.125rem;
  letter-spacing: -0.4px;
  line-height: 1.5rem;
}

@media print, screen and (min-width: 48em) {
  
  h5, .heading04 {
    font-size: 1.25rem;
    letter-spacing: -0.4px;
    line-height: 1.75rem;
  }
}


h6, .heading05 {
  font-weight: normal;
  font-family: "Gotham", Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 600;
  font-size: 1rem;
  letter-spacing: -0.4px;
  line-height: 1.375rem;
}

@media print, screen and (min-width: 48em) {
  
  h6, .heading05 {
    font-size: 1.125rem;
    letter-spacing: -0.4px;
    line-height: 1.5rem;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: inherit;
  line-height: 1;
}
